.paper-shadow {
  position: relative;
  background: #fff;
  /* width: 50%; */
  /* margin: 100px auto; */
  box-shadow: 0px 2px 38px rgba(0, 0, 0, 0.2);
  /* height: 400px; */
}
.paper-shadow:after,
.paper-shadow:before {
  content: "";
  position: absolute;
  left: auto;
  background: none;
  z-index: -1;
}
.paper-shadow:after {
  width: 90%;
  height: 10px;
  top: 30px;
  right: 8px;
  -webkit-transform: rotate(-3deg);
  -moz-transform: rotate(-3deg);
  -o-transform: rotate(-3deg);
  -ms-transform: rotate(-3deg);
  transform: rotate(-3deg);
  -webkit-box-shadow: 0px -20px 36px 5px #295d92;
  -moz-box-shadow: 0px -20px 36px 5px #295d92;
  box-shadow: 0px -25px 35px 0px rgba(0, 0, 0, 0.5);
}

.paper-shadow:before {
  width: 10px;
  height: 95%;
  top: 5px;
  right: 18px;
  -webkit-transform: rotate(3deg);
  -moz-transform: rotate(3deg);
  -o-transform: rotate(3deg);
  -ms-transform: rotate(3deg);
  transform: rotate(3deg);
  -webkit-box-shadow: 20px 0px 25px 5px #295d92;
  -moz-box-shadow: 20px 0px 25px 5px #295d92;
  box-shadow: 22px 0px 35px 0px rgba(0, 0, 0, 0.5);
}

.text-center {
  text-align: center;
}

.text-white {
  color: #fff;
}

.pd-21 {
  padding: 21px;
}

.category-container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}

.subcategory-container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}

.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: 70vh;
  font-size: 20px;
}

.category {
  height: 200px;
  width: 70%;
  /* background: #fff; */
  /* border-radius: 10px; */
  margin-bottom: 30px;
  cursor: pointer;
  /* background-color: #616161; */
  /* box-shadow: 2px 2px #f58345; */
}

.category-image {
  padding: 1px 1px 1px 1px;
  height: 75%;
  width: 100%;
}

.category-name {
  font-family: inherit;
  font-size: 14px;
  height: 25%;
  width: 100%;
  text-align: center;
  font-weight: bold;
  padding: 14px;
  background-color: #5c6bc0;
}

.category-image-logo {
  height: 100%;
  width: 100%;
}

.categoryCard {
  text-align: center;
  border: 1px solid #d2d2d2;
  border-radius: 5px;
  box-shadow: 3px 3px;
}

/* Basic styling */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family: sans-serif;
  font-size: 16px;
}
.nav-bar {
  background: #222;
  padding: 0 15px;
}
span {
  color: white;
  text-decoration: none;
}

a {
  cursor: pointer;
}
.menu,
.submenu {
  list-style-type: none;
}
.logo {
  font-size: 14px;
  padding: 7.5px 10px 7.5px 0;
}
.item {
  padding: 5px;
}
.item.button {
  padding: 9px 5px;
}
.item:not(.button) a:hover,
.item a:hover::after {
  color: #ccc;
}
/* Mobile menu */
.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.menu li span {
  display: block;
  padding: 15px 5px;
}
.menu li.subitem a {
  padding: 15px;
}
.toggle {
  order: 1;
  font-size: 20px;
}
.item.button {
  order: 2;
}
.item {
  order: 3;
  width: 100%;
  text-align: center;
  display: none;
}
.active .item {
  display: block;
}
.button.secondary {
  /* divider between buttons and menu links */
  border-bottom: 1px #444 solid;
}
/* Submenu up from mobile screens */
.submenu {
  display: none;
}
.submenu-active .submenu {
  display: block;
}
.has-submenu i {
  font-size: 12px;
}
.has-submenu > a::after {
  font-family: "Font Awesome 5 Free";
  font-size: 12px;
  line-height: 16px;
  font-weight: 900;
  content: "\f078";
  color: white;
  padding-left: 5px;
}
.subitem a {
  padding: 10px 15px;
}
.submenu-active {
  background-color: #111;
  border-radius: 3px;
}

/* Tablet menu */
@media all and (min-width: 700px) {
  .menu {
    justify-content: center;
  }
  .logo {
    flex: 1;
  }
  .item.button {
    width: auto;
    order: 1;
    display: block;
  }
  .toggle {
    flex: 1;
    text-align: right;
    order: 2;
  }
  /* Button up from tablet screen */
  .menu li.button a {
    padding: 10px 15px;
    margin: 5px 0;
  }
  .button a {
    background: #0080ff;
    border: 1px royalblue solid;
  }
  .button.secondary {
    border: 0;
  }
  .button.secondary a {
    background: transparent;
    border: 1px #0080ff solid;
  }
  .button a:hover {
    text-decoration: none;
  }
  .button:not(.secondary) a:hover {
    background: royalblue;
    border-color: darkblue;
  }
  .category-container {
    display: block;
    margin: 0px 0px 20px 64px;
  }
  .subcategory-container {
    display: block;
  }
  .pd-21 {
    padding: 21px;
    margin-right: 15rem;
  }
}
/* Desktop menu */
@media all and (min-width: 960px) {
  .menu {
    align-items: flex-start;
    flex-wrap: nowrap;
    background: none;
  }
  .logo {
    order: 0;
  }
  .category-container {
    display: block;
    margin: 0px 0px 20px 64px;
  }
  .subcategory-container {
    display: block;
  }
  .item {
    order: 1;
    position: relative;
    display: block;
    width: auto;
  }
  .button {
    order: 2;
  }
  .submenu-active .submenu {
    display: block;
    position: absolute;
    left: 0;
    top: 68px;
    background: #111;
  }
  .toggle {
    display: none;
  }
  .submenu-active {
    border-radius: 0;
  }
  .pd-21 {
    padding: 21px;
    margin-right: 15rem;
  }
}
